import { TitleCasePipe } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { RolesPermissionsService } from 'src/app/core/services/roles-permissions.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { AdminAddRoleFormComponent } from '../admin-add-role-form/admin-add-role-form.component';
import { AdminEditRoleFormComponent } from '../admin-edit-role-form/admin-edit-role-form.component';

@Component({
  selector: 'app-admin-roles-list',
  templateUrl: './admin-roles-list.component.html',
  styleUrls: ['./admin-roles-list.component.scss']
})
export class AdminRolesListComponent implements OnInit {

  public currentUser = JSON.parse(localStorage.getItem("currentUser"));
  public roleData: any;
  public roleId: string="";
  public Id: any;
  showDeleteMessage: boolean=false;
  constructor(public rolePermissionservice: RolesPermissionsService,
    private titleCasePipe: TitleCasePipe, private toastService: ToastNotificationService,private dialog: MatDialog) { }

  ngOnInit(): void {
    this.rolePermissionservice.userAdded$.subscribe(() => {
      this.getRole();
    });
    this.getRole();
  }
  getRole() {
    
    this.rolePermissionservice.getRoles({},this.currentUser.id).then(data => {
      this.roleData = data.data;
     this.roleId=data.data.id;
    },(err) => {
        if(err.error.expose){
          this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
        }
        else{
          this.toastService.openErrorSnackBar("Something Went Wrong.");
        }
        
      })
  }

  deleteRole(){
    this.rolePermissionservice.deleteRoleById({},this.Id).then(data=>{
      this.toastService.openSnackBar("Role Deleted Successfully!!!");
      this.getRole();
     },(err) => {
        if(err.error.expose){
          this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
        }
        else{
          this.toastService.openErrorSnackBar("Something Went Wrong.");
        }
        
      })
  } 
  getRoleID(id){
    this.Id= id
  }

  OpenRoleForm(){
    const dialogRef: MatDialogRef<AdminAddRoleFormComponent> = this.dialog.open(AdminAddRoleFormComponent, {
      width: 'auto',
      height: 'auto',
      data: {},
      panelClass: 'custom-dialog-addRoles-class',
      disableClose:true,
      position: {
        right: '0',
      },
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });
  }
  OpenEditRole(RoleId){
    const dialogRef: MatDialogRef<AdminEditRoleFormComponent> = this.dialog.open(AdminEditRoleFormComponent, {
      width: 'auto',
      height: 'auto',
      data: {RoleId:RoleId},
      panelClass: 'custom-dialog-addeditRoles-class',
      disableClose:true,
      position: {
        right: '0',
      },
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });

  }

  

}
